@import url(https://fonts.googleapis.com/css2?family=Courgette&family=PT+Sans+Caption&display=swap);
body {
  margin: 0;
  font-family: 'PT Sans Caption', sans-serif;
}








.title{
    font-family: 'Courgette', cursive;
    font-size: 2.5rem;
    color: #102a42;
}
.error{
    color:#bb2525;
}
.inputContainer{
    width:90%;
}

@media screen and (min-width:768px) {
    .inputContainer{
        width:70%;
    }
}

@media screen and (min-width:992px) {
    .inputContainer{
        width:50%;
    }
}
.movie-info h4{
    font-size: .875rem;
}

.movie-info{
    color:#fff;
    position: absolute;
    bottom:-40%;
    left:0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.6);
    padding:0.5rem 1rem;
    text-align: left;
    transition: bottom 0.6s;
}

.main-movie-info:hover .movie-info{
    bottom: 0;
}
.home{
    background-color: rgb(4, 28, 50);
    min-height: 100vh;
}
.title {
  font-family: "Courgette", cursive;
  color: rgb(236, 179, 101);
}

.auth {
  min-height: 100vh;
  background-image: linear-gradient(rgb(4, 28, 50), rgb(236, 179, 101));
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.containerForm {
  width: 90%;
  padding: 2rem;
  border-radius: 1rem;
  background-color: rgba(4, 28, 50, 0.7);
  box-shadow: 0 5px 8px 4px rgba(4, 28, 50, 0.4);
}

.button {
  width: 100%;
  padding: 0.5rem;
  border: none;
  outline: none;
  border-radius: 0.2rem;
  background-color: rgb(236, 179, 101);
}

@media screen and (min-width: 768px) {
  .containerForm {
    width: 60%;
  }
}

@media screen and (min-width: 992px) {
  .containerForm {
    width: 30%;
  }
}

.nav{
    background-color: rgb(4, 28, 50);
    box-shadow: 0 3px 8px 1px rgba(255, 255, 255, 0.1);
}

.link{
    color:rgb(236, 179, 101);
    text-decoration: none;
    font-weight: 600;
}

.logo{
    font-family: "Courgette", cursive;
    color:rgb(236, 179, 101);
    font-size: 2.5rem;
}

